import {createSlice, PayloadAction, current} from "@reduxjs/toolkit";
import NeptumImage from "/src/images/Products/NeptunDrum.png";
import {CartItemProps} from "../../components/Cart/components/CartBlock/CartItem/CartItem";

export type InitialStateProps = {
    items: CartItemProps[],
    totalPrice: number,
    openDeliveryModal: boolean,
    formConfig: FormConfigTypes,
    showSuccessModal: boolean
};

export type FormConfigTypes = {
    emailConfig: EmailConfig,
    deliveryConfig: DeliveryConfig,
    payConfig: PayConfig
};

export type EmailConfig = {
    email: string
};

export type DeliveryConfig = {
    deliveryType: `сourier` | `pickUp`,
    formData: CourierForm,
    pickUpData: {
        NAME: string,
        ADDRESS: string
    },
    agreement: boolean;
};

export type PayConfig = {
    PAY_TYPE: string;
}

export type CourierForm = {
    COUNTRY: string,
    CITY: string;
    ADDRESS: string,
    HOUSE: string,
    APARTMENT: string,
    INDEX: string,
    FULL_NAME: string,
    NUMBER: string,
    COMMENT: string,
}

const initialState: InitialStateProps = {
    items: [
        {
            ID: `1A`,
            IMAGE: NeptumImage,
            NAME: `Нептун`,
            PRICE: 9860,
            COUNT: 1
        },
    ],
    formConfig: {
        emailConfig: {
            email: ``
        },
        deliveryConfig: {
            deliveryType: `сourier`,
            formData: {
                COUNTRY: ``,
                CITY: ``,
                ADDRESS: ``,
                HOUSE: ``,
                APARTMENT: ``,
                INDEX: ``,
                FULL_NAME: ``,
                NUMBER: ``,
                COMMENT: ``,
            },
            pickUpData: {
                NAME: ``,
                ADDRESS: ``
            },
            agreement: false
        },
        payConfig: {
            PAY_TYPE: ``
        }
    },
    totalPrice: 0,
    openDeliveryModal: false,
    showSuccessModal: false
};

const cart = createSlice({
    name: `cart`,
    initialState,
    reducers: {
        addToCart: (state: InitialStateProps, action: PayloadAction<CartItemProps>) => {
            state.items.push(action.payload);
            const items = current(state.items);
            state.totalPrice = getTotalPrice(items);
        },
        removeFromCart: (state: InitialStateProps, action: PayloadAction<string>) => {
            const items = current(state.items).filter(item => item.ID !== action.payload);
            state.items = items;
            state.totalPrice = getTotalPrice(items);
        },
        changeCount: (state: InitialStateProps, action: PayloadAction<{ id: string, count: number }>) => {
            const {id, count} = action.payload;

            const items = current(state.items).map(item => {
                if (item.ID !== id) return item;

                if (item.ID === id) {
                    let newItem = {...item};
                    newItem.COUNT = count;
                    return {...newItem};
                }
            });

            state.items = [...items];
            state.totalPrice = getTotalPrice(items);
        },
        setModalActivity: (state: InitialStateProps, action: PayloadAction<boolean>) => {
            state.openDeliveryModal = action.payload;
        },
        changeEmailFormConfig: (state: InitialStateProps, action: PayloadAction<EmailConfig>) => {
            state.formConfig.emailConfig = {...action.payload}
        },
        changeDeliveryFormConfig: (state: InitialStateProps, action: PayloadAction<CourierForm>) => {
            state.formConfig.deliveryConfig.formData = {...action.payload};
            state.formConfig.deliveryConfig.agreement = true;
        },
        changePayFormConfig: (state: InitialStateProps, action: PayloadAction<PayConfig>) => {
            state.formConfig.payConfig = action.payload;
        },
        setShowSuccessModal: (state: InitialStateProps, action: PayloadAction<boolean>) => {
            state.showSuccessModal = action.payload;
        }
    }
})

const getTotalPrice = (items) => {
    if (!items) return;

    let totalPrice: number = 0;

    items.map(item => {
        totalPrice += item.COUNT * item.PRICE
    });

    return totalPrice;
}

export const {
    addToCart,
    removeFromCart,
    changeCount,
    setModalActivity,
    changeEmailFormConfig,
    changeDeliveryFormConfig,
    changePayFormConfig
} = cart.actions;
export default cart.reducer;